    background-position: 102px 41px;
  }
}
@media (min-width: 1440px) {
  .main {
    width: 1440px;
    margin: 0 auto;
    padding: 35px 0 112px 0;
    background: url("../../img/svg/textlogo-desktop.svg");
    background-repeat: no-repeat;
    background-position: 194px 74px;
  }
}
.main__logo {
  position: absolute;
  top: 13px;
  left: 19px;
}
@media (min-width: 768px) {
