}
.slider__item {
  position: absolute;
  top: 0;
  left: 0;
  width: 280px;
  height: 256px;
}
.slider__item::before {
  content: "";
  width: 280px;
  height: 256px;
  position: absolute;
  background-repeat: no-repeat;
  background-position: 0 0;
}
@media (min-width: 768px) {
  .slider__item::before {
    width: 560px;
    height: 512px;
  }
}
@media (min-width: 768px) {
  .slider__item {
    width: 560px;
    height: 512px;
  }
}
.slider__item--before {
  background: #eaeaea;
  z-index: 5;
  width: 50%;
  transition: all 2s;
  overflow: hidden;
  border-right: 4px solid #666666;
}
.slider__item--before::before {
  background-image: url("../img/slider/before-m.png");
}
@media (min-width: 768px) {
  .slider__item--before {
    width: 45%;
    background: linear-gradient(#ffffff 29%, #eaeaea 28%, #eaeaea 70%);
  }
}
@media (min-width: 1440px) {
  .slider__item--before {
    width: 45%;
    background: linear-gradient(#ffffff 26%, #f2f2f2 10%, #f2f2f2 63%);
  }
}
@media (min-width: 768px) {
  .slider__item--before::before {
    background-image: url("../img/slider/before-t.png");
  }
}
.slider--state-before .slider__item--before {
  width: 100%;
}
.slider--state-after .slider__item--before {
  width: 0;
}
.slider__item--after {
  z-index: 1;
}
.slider__item--after::before {
  background-image: url("../img/slider/after-m.png");
}
@media (min-width: 768px) {
  .slider__item--after::before {
    background-image: url("../img/slider/after-t.png");
  }
}
.slider__button {
  width: 40px;
  height: 40px;
  display: block;
  border-radius: 50%;
  border: 2px solid #666666;
  background-image: url("../img/svg/thumb.svg");
  background-repeat: no-repeat;
  background-position-x: center;
  background-position-y: center;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 20;
  cursor: pointer;
  transition: all 2s;
}
